import React from 'react';

// css
import './search-panel.css';

const SearchPanel = () => {
    const styles = {
        display: 'flex',
        padding: '10px',
        boxShadow: '5px 5px 10px rgba(0,0,0, .5)'
    };
    return (
        <div style={styles} className="searchPanel">
            <input type="text" placeholder="search..." />
            <button>search</button>
        </div>
    );
}

export default SearchPanel;