import React, { Component } from 'react';

// css
import classes from './todo-item.module.scss';

export default class TodoItem extends Component {
    state = {
        done: false,
        important: false
    }

    itemDone = () => {
        this.setState( ({done}) => {
            return {
                done: !done
            }
        });
    }
    
    setImportant = () => {
        this.setState( ({important}) => {
            return {
                important: !important
            }
        });
    }

    delItem = () => {
        console.log('del');
    }

    render() {
        const {text, onDelItem} = this.props;
        const {done, important} = this.state;

        // classes
        const {
            todoItem, 
            todoItemDone, 
            todoItemImportant, 
            itemText} = classes;

        let itemClasses = todoItem;

        if (done) {
            itemClasses += ` ${todoItemDone}`;
        }

        if (important) {
            itemClasses += ` ${todoItemImportant}`;
        }

        return (
        <li className={itemClasses}>
            <span  onClick={this.itemDone} className={itemText}>{text}</span>
            <button onClick={this.setImportant}>i</button>
            <button onClick={onDelItem}>del</button>
        </li>
        );
    }
}

